<template>
  <div v-if="lang" class="container-fluid">
    <div class="row">
      <Sidebar :pages="PAGES"/>
      <div class="col-12 px-0">
        <div id="main">
          <Home/>
        </div>
      </div>
    </div>
    </div>
    <div v-else class="container-fluid start">
        <div class="row h-100">
            <div class="offset-4 col-4 h-100">
                <div class="d-flex flex-column justify-content-center align-items-center h-100">
                    <h1 class="text-white text-center">
                        <span class="fw-bold d-block logo-title">1944</span>
                        <span class="fw-bold d-block text-uppercase logo-subtitle">Monte Cassino</span>
                    </h1>
                    <div class="language-selector">
                        <div class="selector" @click="toggleSelector" v-bind:class="{ opened: selectorOpened }">
                            <ul class="language-list text-white">
                                <li v-for="lang in languages" :key="lang.lang" @click="selectLanguage(lang.lang)">
                                    <span class="language-flag">
                                        <img v-bind:src="lang.flag" alt="" />
                                    </span>
                                    <span class="language-text">{{ lang.name }}</span>
                                    <span class="arrow" v-if="lang.order === 0"></span>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <button type="button" class="enter-button text-white" @click="enter">{{ enterText }}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Sidebar from "@/components/Layout/Sidebar";
import {Pages} from "@/components/Consts/Pages"
import {START} from "@/components/Consts/Start";
import Home from "@/components/Pages/Content"

export default {
    name: 'App',
    components: {
        Sidebar,Home
    },
    data() {
        return {
            PAGES: Pages,
            lang: null,
            selectorOpened: false,
            selectedLanguage: `pl`,
            enterText: null,
            languages: [
                {
                    lang: `pl`,
                    flag: require(`./assets/svg/flag-pl.svg`),
                    name: `Polski`,
                    order: 0
                },
                {
                    lang: `en`,
                    flag: require(`./assets/svg/flag-en.svg`),
                    name: `English`,
                    order: 1
                },
                {
                    lang: `it`,
                    flag: require(`./assets/flag-it.png`),
                    name: `Italiano`,
                    order: 2
                }
            ]
        }
    },
    mounted: function() {
        this.enterText = START[this.selectedLanguage].enterText;
    },
    methods: {
        toggleSelector() {
            this.selectorOpened = !this.selectorOpened;
        },
        selectLanguage(language) {
            if (language !== this.selectedLanguage) {
                this.selectedLanguage = language;
                let order = 1;
                this.languages = this.languages.map(lang => {
                    if (lang.lang === language) {
                        lang.order = 0;
                    } else {
                        lang.order = order;
                        order++;
                    }

                    return lang;
                });
                this.languages = this.languages.sort((a, b) => a.order > b.order ? 1 : -1);
                this.enterText = START[this.selectedLanguage].enterText;
            }
        },
        enter() {
            this.lang = this.selectedLanguage;
            this.$store.commit('setLanguage', this.lang);
        }
    },
    watch: {
        '$store.state.selectedLanguage': function () {
        this.lang = this.$store.state.selectedLanguage;
        }
    }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Brygada+1918&display=swap');

html,
body {
  margin: 0;
  box-sizing: border-box;
  background-color: #1d2331 !important;
}

#app {
  font-family: 'Brygada 1918';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100vh;
}

.start {
  background-image: url('./assets/background.jpg');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100%;
}

.logo-title {
  font-size: 180px;
  line-height: 1;
}

.logo-subtitle {
  font-size: 48px;
  line-height: 1;
}

.language-selector {
  margin: 20px 0 0;
  position: relative;
  width: 220px;
  height: 50px;
}

.language-selector .selector {
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  border: 1px solid #fff;
  width: 100%;
  cursor: pointer;
  max-height: 50px;
  border-radius: 2px;
  transition: max-height 0.5s cubic-bezier(0.78, 0, 0.235, 1) 0s;
}

.language-selector .selector.opened {
  max-height: 200px;
}

.language-selector .language-list {
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  z-index: 1;
}

.language-selector .language-list li {
  display: flex;
  height: 50px;
  justify-content: center;
  align-items: center;
  position: relative;
  background: #1b1b1b;
}

.language-selector .language-flag {
  display: block;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 10px;
  transform: translate(0, -50%);
}

.language-selector .language-flag img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.enter-button {
  display: block;
  font-size: 16px;
  text-decoration: none;
  text-transform: none;
  outline: none;
  margin: 20px 0 0;
  padding: 0;
  box-sizing: border-box;
  border: 1px solid #fff;
  cursor: pointer;
  border-radius: 2px;
  height: 50px;
  width: 220px;
  background: #1b1b1b;
}

.arrow {
    width: 0.5rem;
    height: 0.5rem;
    border-top: 1.5px solid rgb(245, 241, 236);
    border-right: 1.5px solid rgb(245, 241, 236);
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translate3d(0px, -70%, 0px) rotate(135deg);
    transition: all 0.5s ease 0s;
}

.language-selector .selector.opened .arrow {
    transform: translate3d(0px, -50%, 0px) rotate(-45deg);
}

.btn:focus,.btn:active {
  outline: none !important;
  box-shadow: none!important;
}
</style>
