<template>
  <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
    <div class="modal-dialog  modal-xl modal-dialog-scrollable modal-dialog-centered" role="document">
      <div class="modal-content image-background-modal text-white">
        <div class="modal-header border-0 justify-content-end">
          <a class="modal-close pe-2" data-bs-dismiss="modal" aria-label="Close">&times;</a>
        </div>
        <div class="modal-body align-content-start">
          <component :is="this.modals[this.componentTitle]"/>
        </div>
        <div class="modal-footer border-0">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {Modals} from "@/components/Consts/Modal";

export default {
  props: ['componentName', 'modalId'],
  data() {
    return {
      componentTitle: '',
      modals: Modals
    }
  },
  mounted() {
    let $vm = this;
    let $ = require('jquery');
    $('[data-bs-toggle="modal"]').on('click', function (e) {
      let $target = $(e.target);
      $vm.componentTitle = $target.data('myvalue');
    });
  }
}
</script>
